@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family:'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

p{
  font-size: 1.2rem;
  line-height: 1.4;
}

h1{
  font-size: 3.5rem;
}

h2{
  font-size: 3rem;
}

.container{
  max-width: 1240px;
  margin: auto;
}

@media screen and (max-width:940px){
  h1{
    font-size: 3rem;
  }

  h2{
    font-size: 2.5rem;
  }
}

.navbar {
    width: 100%;
    height: 100px;
    position: absolute;
    z-index: 2;
    background-color: rgb(34, 39, 74);
    text-shadow: 2px 2px #494747;
}

.navbar .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: .7rem;
}

.navbar .logo{
    display: flex;
    align-items: center;
    color: #fff;
}

.navbar .logo a{
    display: flex;
    align-items: center;
    color: #fff;
}


.navbar .logo .logo-icon{
    font-size: 5rem;
    margin-right: 1rem;
}

.nav-menu-mobile{
    display: none;
    padding: 1rem;
}

.navbar .navbar-menu{
    height: 100%;
    align-items: center;
    display: flex;
    padding: 1rem;
}

.navbar li a{
    padding: 1rem;
    font-weight: 600;
    color: #fff;
    font-size: 1.3rem;
}

.navbar .logo-icon{
    font-size: 1.8rem;
    color: rgb(255, 255, 255);
}

.navbar li a:hover {
    color: gray;
    font-size: 1.6rem;
}


@media screen  and (max-width: 1100px) {
    .navbar .nav-menu-mobile{
        display: block;
        z-index: 999;
    }

    .navbar .navbar-menu {
        position: absolute;
        flex-direction: column;
        background: black !important;
        width: 100%;
        height: 100vh;
        z-index: 99;
        top: 0;
        left: -100%;
        align-items: center;
        justify-content: center;
    }

    .navbar .active{
        left: 0 !important;
    }

    .navbar li a{
        font-size: 2rem;
    }

    .navbar li{
        margin: 1rem;
        font-size: 3rem;
    }

}
    

@media screen  and (max-width: 450px) {

    .navbar .logo .logo-icon{
        font-size: 3rem;     
    }

    .navbar .logo a h1{
        font-size: 1.6rem;
    }


}
.footer-container{
    height: 35vh;
    background-color: rgb(34, 39, 74);
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px #000000;
    position: absolute;
    width:100%;
}

.footer-container .footer-sections{
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 30vh;
    text-align: center;
    font-size: 22px;
    font-weight: 700;
}

.footer-container .footer-sections .footer-right{
    height: 100%;
    width: 34%;
}

.footer-container .footer-middle{
    height: 100%;
    width: 33%;
}

.footer-container .footer-left .title p{
    font-size: 30px;
    padding: 10px 0 0 50%;
    text-decoration: underline;
}

.footer-container .footer-middle .title p{
    font-size: 30px;
    padding: 10px 0 0 0;
    text-decoration: underline;
}

.footer-container .footer-right .title p{
    font-size: 30px;
    padding: 10px 50% 0 0;
    text-decoration: underline;
}

.footer-container .footer-left{
    height: 100%;
    width: 33%;
}

.footer-left .footer-links li{
    padding: 10px 0 0 50%;
}

.footer-middle .footer-links li{
    padding: 15px 0 0 0;
}

.footer-right .footer-links li{
    padding: 12px 50% 0 0;
}

.footer-middle p{
    font-size: 15px;
    padding: 3rem 0 0 0;
}

.footer-left .footer-links li a:hover {
    color: gray;
    font-size: 1.7rem;
}

.footer-middle .footer-links li a:hover {
    color: gray;
    font-size: 1.7rem;
}

.footer-right .footer-links li a:hover {
    color: gray;
    font-size: 1.7rem;
}

.footer-left .footer-links li a{
    color: white;
    font-size: 20px;
}

.footer-middle .footer-links li a{
    color: white;
    font-size: 20px;
}

.footer-right .footer-links li a{
    color: white;
    font-size: 20px;
}


@media screen  and (max-width: 940px) {
    .footer-left .footer-links li a, .footer-middle .footer-links li a, .footer-right .footer-links li a{
        font-size: 15px;
    }

    .footer-container .footer-left .title p, .footer-container .footer-middle .title p, .footer-container .footer-right .title p{
        font-size: 20px;
    }
    
    .footer-left .footer-links li a:hover, .footer-middle .footer-links li a:hover, .footer-right .footer-links li a:hover {
        font-size: 1rem;
    }

    .footer-left .footer-links li{
        padding: 5px 0 0 50%;
    }
    
    .footer-middle .footer-links li{
        padding: 5px 0 0 0;
    }
    
    .footer-right .footer-links li{
        padding: 5px 50% 0 0;
    }

}

@media screen  and (max-width: 450px) {
    .footer-container{
        height: 45vh;
    }

    .footer-container .footer-left{
        font-size: ;
        width: 33%;
        padding-right: 10%;
    }

    .footer-container .footer-left .title p{

        padding: 10px 0 0 20%;
    }

    .footer-container .footer-sections .footer-right{
        height: 100%;
        width: 33%;
        padding-left: 10%;
    }

    .footer-container .footer-left .title p{
        padding: 10px 0 0 30%;
    }
    
    .footer-container .footer-middle{
        height: 100%;
        width: 33%;
    }

    .footer-container .footer-right .title p{
        padding: 10px 20% 0 0;
    }

    .footer-left .footer-links li a, .footer-middle .footer-links li a, .footer-right .footer-links li a{
        font-size: 13px;
    }

    .footer-container .footer-left .title p, .footer-container .footer-middle .title p, .footer-container .footer-right .title p{
        font-size: 17px;
    }
    
    .footer-left .footer-links li a:hover, .footer-middle .footer-links li a:hover, .footer-right .footer-links li a:hover {
        font-size: 0.8rem;
    }

}
.auth-container{
    height: 100vh;
    background-color: rgb(56, 129, 178);
    color: white;
}

.auth-wrapper{
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    height: 90%;
    width: 40%;
}

.auth-wrapper .auth-section{
    display: flex;
    border: 2px solid white;
    background-color: rgb(10, 73, 116);
    box-shadow: 10px 7px 9px rgb(45, 44, 44);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 10%;
}

.auth-section h1{
    padding-top: .8rem;
