.contact2-container{
    height: 100vh;
    background-color: rgb(56, 129, 178);
    color: rgb(0, 0, 0);
}

.contact2-container .contact-content{
    display: flex;
    flex-direction: space-between;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.contact2-container .contact-left{
    height: 70%;
    width: 50%;
    padding-left: 10rem;
    text-align: center;
    color: rgb(0, 0, 0);
}

.contact2-container .contact-right{
    height: 70%;
    width: 50%;
    float: left;
}

.contact2-container .contact-content .contact-left h1{
    font-size: 3rem;
    text-align: center;
    color: rgb(251, 253, 255);
    padding: 2rem 0 0rem 0;
    text-shadow: 2px 2px #000000;
}

.contact2-container .contact-content .contact-left .contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-shadow: 2px 2px #000000;
    color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
}

.contact2-container .contact-content .contact-left label{
    padding-top: 1.3rem;
    font-size: 1.5rem;
}

.contact2-container .contact-content .contact-left input{
    width: 40%;
    box-shadow: 3px 4px 4px rgb(9, 9, 9);
}

.contact2-container .contact-content .contact-left textarea{
    width: 40%;
    height: 200px;
    box-shadow: 3px 4px 4px rgb(9, 9, 9);
}

.contact2-container .contact-content .contact-left .btn{
    margin-top: 1rem;
    font-size: 1.2rem;
    border-radius: 15%;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    padding: .5rem;

}

.contact2-container img{
    height: 450px;
    margin-top: 2vh;
    border-radius: 25%;
    background-image: linear-gradient(rgb(19, 149, 230), rgb(73, 75, 74));
    border: 6px solid rgb(24, 23, 23);

}

@media screen and (max-width: 940px){
    
    .contact2-container img{
        display: none;
    }

    .contact2-container .contact-left{
        width: 100%;
        padding: 0px 0 0 25%;
    }

    .contact2-container .contact-content .contact-left textarea, input{
        width: 100%;
    }

    .contact2-container .contact-content .contact-left input{
        width: 100%;
    }
}

/*This sets the hight of the app to 100vh*/
.app{
    height: 100vh;
}

/*This sets the hight of the app sections to 100vh and also makes scroll smoother */
.sections{
    width: 100%;
    height: calc(100vh);
    /* background-color: lightsalmon; */
    position: relative;
    top: 100px;
    scroll-behavior: smooth;
    scrollbar-width: none;
}

html {scroll-snap-type: y mandatory}
 
/* gets rid of scrollbar for better visual affect */
::-webkit-scrollbar{
    display: none;
}

